import { EVT } from './EVT';
// 
class Store {
    constructor() {
        this.postIdList = [];
        /** */
        this.resultMeta = [];
        /** */
        this.result = [];
        /** */
        this.date = new Date();
        /** */
        this.skipDueToFee = 0;
        this.bindEvents();
    }
    bindEvents() {
        window.addEventListener(EVT.list.crawlStart, () => {
            this.resetResult();
        });
    }
    getCommonData(data) {
        return {
            postId: data.postId,
            type: data.type,
            title: data.title,
            date: data.date,
            fee: data.fee,
            user: data.user,
            uid: data.uid,
            createID: data.createID,
            tags: data.tags,
        };
    }
    // 
    addResult(data) {
        this.resultMeta.push(data);
        //   
        //  URL URL
        if (data.links.text.length > 0) {
            const result = Object.assign(this.getCommonData(data), data.links);
            this.result.push(result);
        }
        //  files 
        const files = data.files;
        for (const fileData of files) {
            const result = Object.assign(this.getCommonData(data), fileData);
            this.result.push(result);
        }
    }
    resetResult() {
        this.postIdList = [];
        this.resultMeta = [];
        this.result = [];
        this.skipDueToFee = 0;
    }
}
const store = new Store();
export { store };
