import { log } from './Log';
import { EVT } from './EVT';
import { Config } from './Config';
import { settings } from './setting/Settings';
import { msgBox } from './MsgBox';
import { lang } from './Lang';
// 
class Filter {
    constructor() {
        this.bindEvents();
    }
    // 
    showTip() {
        this.getFeeType();
        this.getFeeRange();
        this.getIdRange();
        this.getPostDate();
        this.getTitleMustText();
        this.getTitleCannotText();
    }
    getFeeType() {
        if (settings.free && settings.pay) {
            return;
        }
        let msg = '';
        if (settings.free) {
            msg = `${lang.transl('_')}: ${lang.transl('_')}`;
        }
        if (settings.pay) {
            msg = `${lang.transl('_')}: ${lang.transl('_')}`;
        }
        log.warning(msg);
    }
    getFeeRange() {
        if (!settings.feeSwitch) {
            return;
        }
        const msg = `${lang.transl('_')}: ${lang.transl('_')} ${settings.fee}`;
        log.warning(msg);
    }
    //  id 
    getIdRange() {
        if (!settings.idRangeSwitch) {
            return;
        }
        log.warning(`id ${settings.idRange} ${settings.idRangeInput}`);
    }
    // 
    getPostDate() {
        if (!settings.postDate) {
            return;
        }
        if (isNaN(settings.postDateStart) || isNaN(settings.postDateStart)) {
            const msg = lang.transl('_');
            this.showWarning(msg);
        }
        else {
            const start = new Date(settings.postDateStart).toLocaleString();
            const end = new Date(settings.postDateEnd).toLocaleString();
            log.warning(`${lang.transl('_')}: ${start} - ${end}`);
        }
    }
    getTitleMustText() {
        if (!settings.titleMustTextSwitch) {
            return;
        }
        const msg = `${lang.transl('_')}: ${settings.titleMustText.toString()}`;
        log.warning(msg);
    }
    getTitleCannotText() {
        if (!settings.titleCannotTextSwitch) {
            return;
        }
        const msg = `${lang.transl('_')}: ${settings.titleCannotText.toString()}`;
        log.warning(msg);
    }
    // 
    //  FilterOption 
    check(option) {
        if (!this.checkFileType(option.ext)) {
            return false;
        }
        if (!this.checkfeeType(option.fee)) {
            log.warning(lang.transl('_', option.title) + lang.transl('_'));
            return false;
        }
        if (!this.checkfeeRange(option.fee)) {
            log.warning(lang.transl('_', option.title) + lang.transl('_'));
            return false;
        }
        if (!this.checkIdRange(option.id)) {
            log.warning(lang.transl('_', option.title) + lang.transl('_id'));
            return false;
        }
        if (!this.checkPostDate(option.date)) {
            log.warning(lang.transl('_', option.title) + lang.transl('_'));
            return false;
        }
        if (!this.checkTitltMustText(option.title)) {
            log.warning(lang.transl('_', option.title) +
                lang.transl('_'));
            return false;
        }
        if (!this.checkTitltCannotText(option.title)) {
            log.warning(lang.transl('_', option.title) +
                lang.transl('_'));
            return false;
        }
        return true;
    }
    checkFileType(ext) {
        //  ext
        if (!ext) {
            return true;
        }
        //  ext 
        for (const [key, value] of Object.entries(Config.fileType)) {
            if (value.includes(ext)) {
                return settings[key];
            }
        }
        //  ext  other 
        return settings.other;
    }
    checkfeeType(fee) {
        if (fee === undefined) {
            return true;
        }
        if (fee > 0) {
            return settings.pay;
        }
        else {
            return settings.free;
        }
    }
    checkfeeRange(fee) {
        if (fee === undefined || !settings.feeSwitch) {
            return true;
        }
        return fee >= settings.fee;
    }
    checkIdRange(id) {
        if (id === undefined || !settings.idRangeSwitch) {
            return true;
        }
        const nowId = parseInt(id.toString());
        const setId = settings.idRangeInput;
        if (settings.idRange === '>') {
            return nowId > setId;
        }
        else {
            return nowId < setId;
        }
    }
    checkPostDate(date) {
        if (!settings.postDate ||
            date === undefined ||
            !settings.postDateStart ||
            !settings.postDateEnd) {
            return true;
        }
        const nowDate = new Date(date);
        return (nowDate.getTime() >= settings.postDateStart &&
            nowDate.getTime() <= settings.postDateEnd);
    }
    checkTitltMustText(title) {
        if (!settings.titleMustTextSwitch ||
            !title ||
            settings.titleMustText.length === 0) {
            return true;
        }
        title = title.toLowerCase();
        const match = settings.titleMustText.filter((str) => title.includes(str.toLowerCase()));
        if (match.length === 0) {
            return false;
        }
        return true;
    }
    checkTitltCannotText(title) {
        if (!settings.titleCannotTextSwitch ||
            !title ||
            settings.titleCannotText.length === 0) {
            return true;
        }
        title = title.toLowerCase();
        const match = settings.titleCannotText.filter((str) => title.includes(str.toLowerCase()));
        if (match.length > 0) {
            return false;
        }
        return true;
    }
    // 
    showWarning(msg) {
        EVT.fire('wrongSetting');
        msgBox.error(msg);
    }
    bindEvents() {
        window.addEventListener(EVT.list.crawlStart, () => {
            this.showTip();
        });
    }
}
const filter = new Filter();
export { filter };
