// 
import { lang } from './Lang';
import { Tools } from './Tools';
import { filter } from './Filter';
import { store } from './Store';
import { log } from './Log';
import { EVT } from './EVT';
import { saveData } from './SaveData';
import { API } from './API';
import { states } from './States';
import { msgBox } from './MsgBox';
import { toast } from './Toast';
import { Utils } from './utils/Utils';
class InitPageBase {
    constructor() {
        this.crawlNumber = 0; // /
        this.nextUrl = null;
        this.getPostDataThreadMax = 6;
        this.getPostDataThreadNum = 0;
        this.getPostDatafinished = 0;
    }
    // 
    init() {
        this.addCrawlBtns();
        this.addAnyElement();
        this.initAny();
        window.addEventListener(EVT.list.pageSwitchedTypeChange, () => {
            this.destroy();
        });
    }
    // 
    initAny() { }
    // 
    destroy() {
        Tools.clearSlot('crawlBtns');
    }
    // 
    addCrawlBtns() { }
    // 
    addAnyElement() { }
    // 
    async readyCrawl() {
        if (states.busy) {
            window.alert(lang.transl('_2'));
            return;
        }
        log.clear();
        log.success(lang.transl('_'));
        toast.show(lang.transl('_'));
        EVT.fire('crawlStart');
        this.getPostDataThreadNum = 0;
        this.getPostDatafinished = 0;
        this.nextUrl = null;
        // 
        this.nextStep();
    }
    // 
    nextStep() {
        this.FetchPostList();
    }
    afterFetchPostList(data) {
        if (data.body.items.length === 0) {
            return this.noResult();
        }
        const items = data.body.items;
        this.nextUrl = data.body.nextUrl;
        for (const item of items) {
            if (item.body === null) {
                continue;
            }
            // 
            const id = item.id;
            const fee = item.feeRequired;
            const date = item.publishedDatetime;
            const title = item.title;
            const check = filter.check({ id, fee, date, title });
            if (check) {
                store.postIdList.push(id);
            }
        }
        if (this.nextUrl) {
            this.FetchPostList();
        }
        else {
            this.FetchPostListFinished();
        }
    }
    // 
    FetchPostListFinished() {
        log.log(lang.transl('_'));
        if (store.postIdList.length === 0) {
            return this.noResult();
        }
        log.log(lang.transl('_', store.postIdList.length.toString()));
        log.log(lang.transl('_'));
        for (let i = 0; i < this.getPostDataThreadMax; i++) {
            const postId = store.postIdList.shift();
            if (postId) {
                this.getPostDataThreadNum++;
                this.fetchPost(postId);
            }
            else {
                break;
            }
        }
    }
    async fetchPost(postId) {
        const data = await API.getPost(postId);
        this.afterFetchPost(data);
    }
    afterFetchPost(data) {
        saveData.receive(data.body);
        log.log(`${lang.transl('_')} ${store.postIdList.length}`, 1, false);
        // 
        // 
        const postId = store.postIdList.shift();
        if (postId) {
            this.fetchPost(postId);
        }
        else {
            this.getPostDatafinished++;
            if (this.getPostDatafinished == this.getPostDataThreadNum) {
                this.crawlFinished();
            }
        }
    }
    // 
    crawlFinished() {
        if (store.skipDueToFee > 0) {
            log.warning(lang.transl('_') + store.skipDueToFee);
        }
        if (store.result.length === 0) {
            return this.noResult();
        }
        //  postid 
        store.result.sort(Utils.sortByProperty('postId', 'asc'));
        store.date = new Date();
        log.log(lang.transl('_', store.result.length.toString()));
        log.success(lang.transl('_'), 2);
        EVT.fire('crawlFinish');
    }
    //  0 
    noResult() {
        EVT.fire('crawlFinish');
        EVT.fire('crawlEmpty');
        log.error(lang.transl('_'), 2);
        msgBox.error(lang.transl('_'));
    }
}
export { InitPageBase };
