import { EVT } from './EVT';
import { lang } from './Lang';
import { settings } from './setting/Settings';
import { Tools } from './Tools';
class ShowNotification {
    constructor() {
        this.iconURL = '';
        this.iconURL = chrome.runtime.getURL('icon/logo128.png');
        this.bindEvents();
    }
    bindEvents() {
        // 
        window.addEventListener(EVT.list.settingChange, (ev) => {
            const data = ev.detail.data;
            if (data.name === 'showNotificationAfterDownloadComplete' && data.value) {
                this.requstPremission();
            }
        });
        // 
        window.addEventListener(EVT.list.downloadComplete, () => {
            window.setTimeout(() => {
                // 
                //  crawlTagList 
                // 
                if (settings.showNotificationAfterDownloadComplete) {
                    this.show(lang.transl('_2'), Tools.getPageTitle());
                }
            }, 0);
        });
    }
    async show(title, text) {
        await this.requstPremission();
        new Notification(title, {
            body: text,
            //  tag tag
            // tag: '',
            icon: this.iconURL,
        });
    }
    requstPremission() {
        if (Notification.permission !== 'granted') {
            return Notification.requestPermission();
        }
    }
}
new ShowNotification();
