import { Colors } from './Colors';
// 
// 
class Toast {
    constructor() {
        this.defaultCfg = {
            msg: '',
            color: Colors.white,
            bgColor: Colors.bgBrightBlue,
            dealy: 1500,
            enter: 'up',
            leave: 'fade',
            position: 'mouse',
        };
        this.successCfg = {
            msg: '',
            color: Colors.white,
            bgColor: Colors.bgSuccess,
            dealy: 1500,
            enter: 'up',
            leave: 'fade',
            position: 'mouse',
        };
        this.warningCfg = {
            msg: '',
            color: Colors.white,
            bgColor: Colors.bgWarning,
            dealy: 1500,
            enter: 'up',
            leave: 'fade',
            position: 'mouse',
        };
        this.errorCfg = {
            msg: '',
            color: Colors.white,
            bgColor: Colors.bgError,
            dealy: 1500,
            enter: 'up',
            leave: 'fade',
            position: 'mouse',
        };
        this.tipClassName = 'xzToast';
        this.mousePosition = { x: 0, y: 0 };
        this.minTop = 20;
        this.once = 1; // 
        this.total = 20; // 
        this.bindEvents();
    }
    bindEvents() {
        //  mousemove  click
        window.addEventListener('mousemove', (ev) => {
            this.mousePosition.x = ev.x;
            this.mousePosition.y = ev.y;
        });
    }
    show(msg, arg) {
        this.create(Object.assign({}, this.defaultCfg, arg, { msg: msg }));
    }
    success(msg, arg) {
        this.create(Object.assign({}, this.successCfg, arg, { msg: msg }));
    }
    warning(msg, arg) {
        this.create(Object.assign({}, this.warningCfg, arg, { msg: msg }));
    }
    error(msg, arg) {
        this.create(Object.assign({}, this.errorCfg, arg, { msg: msg }));
    }
    create(arg) {
        const span = document.createElement('span');
        span.textContent = arg.msg;
        span.style.color = arg.color;
        //  color
        span.style.backgroundColor = arg.bgColor;
        span.style.opacity = '0'; // 
        // 
        span.classList.add(this.tipClassName);
        document.body.appendChild(span);
        //  left
        // 
        let centerPoint = window.innerWidth / 2;
        if (arg.position === 'mouse') {
            // 
            centerPoint = this.mousePosition.x;
        }
        //  left
        const rect = span.getBoundingClientRect();
        let left = centerPoint - rect.width / 2;
        const minLeft = 0; // 
        const maxLeft = window.innerWidth - rect.width; // 
        if (left < minLeft) {
            left = minLeft;
        }
        if (left > maxLeft) {
            left = maxLeft;
        }
        span.style.left = left + 'px';
        //  top
        let lastTop = 0;
        if (arg.position === 'topCenter') {
            lastTop = this.minTop;
        }
        if (arg.position === 'center') {
            lastTop = window.innerHeight / 2 - this.minTop;
        }
        if (arg.position === 'mouse') {
            // 
            // top 
            let y = this.mousePosition.y - 40;
            if (y < this.minTop) {
                y = this.minTop;
            }
            lastTop = y;
        }
        // 
        if (arg.enter === 'none') {
            span.style.top = lastTop + 'px';
            span.style.opacity = '1';
        }
        else {
            this.enter(span, arg.enter, lastTop);
        }
        // 
        window.setTimeout(() => {
            if (arg.leave === 'none') {
                span.remove();
            }
            else {
                this.leave(span, arg.leave, lastTop);
            }
        }, arg.dealy);
    }
    // 
    enter(el, way, lastTop) {
        const startTop = lastTop + this.total; //  top 
        const once = 2;
        const total = this.total;
        let numberOfTimes = 0; // 
        const frame = function (timestamp) {
            numberOfTimes++;
            // 
            const move = once * numberOfTimes;
            // 
            const opacity = move / total;
            if (move <= total && opacity <= 1) {
                if (way === 'up') {
                    el.style.top = startTop - move + 'px';
                }
                el.style.opacity = opacity.toString();
                // 
                window.requestAnimationFrame(frame);
            }
        };
        window.requestAnimationFrame(frame);
    }
    // 
    leave(el, way, lastTop) {
        const startTop = lastTop; //  top 
        const once = this.once;
        const total = this.total;
        let numberOfTimes = 0; // 
        const frame = function (timestamp) {
            numberOfTimes++;
            // 
            const move = once * numberOfTimes;
            // 
            const opacity = 1 - move / total;
            if (move < total && opacity > 0) {
                if (way === 'up') {
                    el.style.top = startTop - move + 'px';
                }
                el.style.opacity = opacity.toString();
                // 
                window.requestAnimationFrame(frame);
            }
            else {
                // 
                el.remove();
            }
        };
        window.requestAnimationFrame(frame);
    }
}
const toast = new Toast();
export { toast };
