import { lang } from './Lang';
import { Config } from './Config';
import { msgBox } from './MsgBox';
import { EVT } from './EVT';
import { setSetting, settings } from './setting/Settings';
// 
class ShowWhatIsNew {
    constructor() {
        this.flag = '3.3.0';
        this.bindEvents();
    }
    bindEvents() {
        window.addEventListener(EVT.list.settingInitialized, () => {
            //  settingInitialized 
            let msg = `<strong>${lang.transl('_')}</strong>
      <br>
      <span class="blue">{create_id}</span> ${lang.transl('_create_id')}
      `;
            // 
            msg += `
      <br>
      <br>
      ${lang.transl('_')}`;
            this.show(msg);
        });
    }
    show(msg) {
        if (settings.whatIsNewFlag !== this.flag) {
            msgBox.show(msg, {
                title: Config.appName + ` ${lang.transl('_')}`,
                btn: lang.transl('_'),
            });
            setSetting('whatIsNewFlag', this.flag);
        }
    }
}
new ShowWhatIsNew();
