import { EVT } from '../EVT';
import { settings, setSetting } from './Settings';
import { DateFormat } from '../utils/DateFormat';
import { nameRuleManager } from './NameRuleManager';
class FormSettings {
    constructor(form) {
        //  userSetName  nameRuleManager 
        this.inputFileds = {
            checkbox: [
                'image',
                'music',
                'video',
                'compressed',
                'ps',
                'other',
                'free',
                'pay',
                'feeSwitch',
                'idRangeSwitch',
                'postDate',
                'saveLink',
                'saveText',
                'autoStartDownload',
                'showAdvancedSettings',
                'bgDisplay',
                'boldKeywords',
                'showNotificationAfterDownloadComplete',
                'zeroPadding',
                'deduplication',
                'savePostCover',
                'unifiedURL',
                'titleMustTextSwitch',
                'titleCannotTextSwitch',
            ],
            text: [
                'fee',
                'idRangeInput',
                'downloadThread',
                'dateFormat',
                'bgOpacity',
                'zeroPaddingLength',
                'titleMustText',
                'titleCannotText',
            ],
            radio: ['idRange', 'bgPositionY', 'userSetLang'],
            textarea: [],
            datetime: ['postDateStart', 'postDateEnd'],
        };
        this.restoreTimer = 0;
        this.form = form;
        nameRuleManager.registerInput(this.form.userSetName);
        this.bindEvents();
        this.restoreFormSettings();
        this.ListenChange();
    }
    bindEvents() {
        window.addEventListener(EVT.list.settingChange, () => {
            window.clearTimeout(this.restoreTimer);
            this.restoreTimer = window.setTimeout(() => {
                this.restoreFormSettings();
            }, 0);
        });
    }
    // 
    // 
    ListenChange() {
        for (const name of this.inputFileds.text) {
            this.saveTextInput(name);
        }
        for (const name of this.inputFileds.textarea) {
            this.saveTextInput(name);
        }
        for (const name of this.inputFileds.datetime) {
            this.saveTextInput(name);
        }
        for (const name of this.inputFileds.radio) {
            this.saveRadio(name);
        }
        for (const name of this.inputFileds.checkbox) {
            this.saveCheckBox(name);
        }
    }
    // 
    restoreFormSettings() {
        for (const name of this.inputFileds.text) {
            this.restoreString(name);
        }
        for (const name of this.inputFileds.radio) {
            this.restoreString(name);
        }
        for (const name of this.inputFileds.textarea) {
            this.restoreString(name);
        }
        for (const name of this.inputFileds.checkbox) {
            this.restoreBoolean(name);
        }
        for (const name of this.inputFileds.datetime) {
            this.restoreDate(name);
        }
    }
    // ---------------------
    //  change  value
    saveTextInput(name) {
        const el = this.form[name];
        el.addEventListener('change', () => {
            setSetting(name, el.value);
        });
    }
    //  click  checked
    saveCheckBox(name) {
        const el = this.form[name];
        el.addEventListener('click', () => {
            setSetting(name, el.checked);
        });
    }
    //  click  value
    saveRadio(name) {
        const radios = this.form[name];
        for (const radio of radios) {
            radio.addEventListener('click', () => {
                setSetting(name, radio.value);
            });
        }
    }
    //  Boolean 
    restoreBoolean(name) {
        if (settings[name] !== undefined) {
            this.form[name].checked = settings[name];
        }
    }
    //  string 
    restoreString(name) {
        if (settings[name] !== undefined) {
            this.form[name].value = settings[name].toString();
        }
    }
    // 
    restoreDate(name) {
        if (settings[name] !== undefined) {
            //  input 
            const date = settings[name];
            this.form[name].value = DateFormat.format(date, 'YYYY-MM-DDThh:mm');
        }
    }
}
export { FormSettings };
