class Config {
}
/** txt  */
Config.outputMax = 5000;
/** */
Config.downloadThreadMax = 6;
/** */
Config.retryMax = 10;
/** */
Config.appName = 'Pixiv Fanbox Downloader';
/** localStorage  name */
Config.settingStoreName = 'fanboxSetting';
/**fanbox  */
Config.fileType = {
    image: ['jpg', 'jpeg', 'png', 'gif', 'bmp'],
    music: ['wav', 'mp3', 'flac'],
    video: ['mp4', 'mov', 'avi'],
    compressed: ['zip'],
    ps: ['psd', 'clip'],
    other: ['txt', 'pdf'],
};
/** */
Config.defaultNameRule = '{user}/{date}-{title}/{index}';
export { Config };
