import { EVT } from './EVT';
class UnifiedURL {
    constructor() {
        this.bindEvents();
    }
    bindEvents() {
        window.addEventListener(EVT.list.settingChange, (ev) => {
            const data = ev.detail.data;
            if (data.name === 'unifiedURL' && data.value) {
                this.check();
            }
        });
    }
    check() {
        // 
        // https://www.fanbox.cc/
        const test = location.hostname.match(/(.*)\.fanbox.cc/);
        if (!test || test.length < 2) {
            return;
        }
        const subDomain = test[1];
        // 
        if (subDomain === 'www' ||
            subDomain === 'api' ||
            subDomain === 'downloads') {
            return;
        }
        // 
        // 
        // fanbox 
        const metaElement = document.head.querySelector('meta#metadata');
        if (!metaElement) {
            return;
        }
        const content = metaElement.getAttribute('content');
        if (!content) {
            return;
        }
        const data = JSON.parse(content);
        // null  string id
        if (data.context.user.userId === null) {
            return;
        }
        // path  404path 
        let path = location.pathname;
        if (path.endsWith('/')) {
            path = path.substring(0, path.length - 1);
        }
        //  https://www.fanbox.cc/ 
        const newURL = `https://www.fanbox.cc/@${subDomain}` + path;
        location.href = newURL;
    }
}
new UnifiedURL();
